import React, { Component } from "react";
import TextField from "@material-ui/core/TextField";
import { Container } from "@material-ui/core";
import "../AddModal/AddModal.scss";
import { withStyles } from "@material-ui/core/styles";
import TextareaAutosize from "@material-ui/core/TextareaAutosize";
import { connect } from "react-redux";
import { handleEdit } from "../../config/redux/action";

const CssTextField = withStyles({
  root: {
    "& label.Mui-focused": {
      color: "#f9a826",
    },
    "& .MuiInput-underline:after": {
      borderBottomColor: "#f9a826",
    },
    "& .MuiOutlinedInput-root": {
      "& fieldset": {
        borderColor: "#f9a826",
        borderRadius: "10px",
      },
      "&:hover fieldset": {
        borderColor: "#f9a826",
      },
      "&.Mui-focused fieldset": {
        borderColor: "#f9a826",
      },
    },
  },
})(TextField);

class EditModal extends Component {
  handleModal = () => {
    this.props.setIsModal(false);
  };
  render() {
    console.log("umplog");
    const {handleEditSubmit, formData, handleChange} = this.props;
    return (
      <div className="add-modal">
        <Container maxWidth="sm">
          <div className="modal-wrapper">
            <form onSubmit={handleEditSubmit} className="form">
              <CssTextField
                label="Judul"
                variant="outlined"
                id="custom-css-outlined-input"
                value={formData.heading}
                onChange={handleChange}
                name="heading"
                required
              />
              <TextareaAutosize
                aria-label="minimum height"
                minRows={4}
                placeholder="Note in here"
                className="textarea"
                value={formData.content}
                onChange={handleChange}
                name="content"
                required
              />
              <div className="btn-modal">
                <button onClick={this.handleModal} className="btn-cancle-modal">
                  Cancle
                </button>
                <button type="submit" className="btn-submit-modal">
                  Submit
                </button>
              </div>
            </form>
          </div>
        </Container>
      </div>
    );
  }
}
const reduxDispatch = (dispatch) => ({
  setIsModal: (data) => dispatch(handleEdit(data)),
});

export default connect(null, reduxDispatch)(EditModal);
