import React from "react";
import "./NavbarHome.scss";
import { useDispatch, useSelector } from "react-redux";
import { LogoutButton } from "../ButtonNotes";
import { LogoutUserAPI } from "../../config/redux/action";
import { useHistory } from "react-router-dom";
import swal from "sweetalert";

const NavbarHome = ({ Search }) => {
  const userData = JSON.parse(localStorage.getItem("userData"));
  const dispatch = useDispatch();
  const isLogin = useSelector((state) => state.isLogin);
  const history = useHistory();
  const goToHome = () => {
    if (isLogin) {
      history.push(`/notes/${userData.userId}`);
    } else {
      history.push("/");
    }
  };
  const handleLogout = () => {
    swal({
      title: "Apakah anda ingin Keluar?",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        dispatch(LogoutUserAPI());
        history.push(`/`);
        swal("Anda berhasil keluar", {
          icon: "success",
          buttons: false,
          timer: 1500,
        });
      } else {
        swal({
          text: "Anda gagal keluar",
          timer: 1500,
          buttons: false,
        });
      }
    });
  };
  console.log("isLogin: ", isLogin);
  return (
    <nav>
      <div className="navbar-home">
        <div className="logo-navbar" onClick={goToHome}>
          <h1>Catatain</h1>
        </div>
        <div className="links-navbar">
          {Search}
          <div className="profile">
            {isLogin ? <LogoutButton onClick={handleLogout} /> : null}
          </div>
        </div>
      </div>
    </nav>
  );
};

export default NavbarHome;
