import React, { Component, Fragment } from "react";
import { Container } from "@material-ui/core";
import {
  BackNotes,
  EditNotes,
  DeleteNotes,
} from "../../components/ButtonNotes";
import TodayIcon from "@material-ui/icons/Today";
import "./Notes.scss";
import swal from "sweetalert";
import EditModal from "../../components/EditModal";
import {
  handleEdit,
  getDataNotesFromAPI,
  PutDataToAPI,
  DeleteDataToAPI,
} from "../../config/redux/action";
import { connect } from "react-redux";
import { formatDate, timestamp } from "../../utils";

class Notes extends Component {
  state = {
    notes: {
      heading: "",
      content: "",
      date: "",
    },
    formData: {
      heading: "",
      content: "",
    },
    isUpdate: false,
  };
  componentDidMount = async () => {
    const id = this.props.match.params.id;
    const userData = JSON.parse(localStorage.getItem("userData"));
    const userId = userData.userId;
    console.log(this.props);
    const res = await this.props.getDataNotesFromAPI({ userId, id });
    if (res) {
      const data = {
        heading: res.heading,
        content: res.content,
        date: res.date,
      };
      this.setState({
        notes: data,
        formData: data,
      });
      console.log(res);
    }
  };
  handleDelete = () => {
    const id = this.props.match.params.id;
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this imaginary file!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        const userData = JSON.parse(localStorage.getItem("userData"));
        const data = {
          userId: userData.userId,
          noteId: id,
        };
        this.props.DeleteDataToAPI(data);
        this.props.history.push(`/notes/${userData.userId}`);
        swal("Poof! Your imaginary file has been deleted!", {
          icon: "success",
          buttons: false,
          timer: 1500,
        });
      } else {
        swal({
          text: "Your imaginary file is safe!",
          timer: 1500,
          buttons: false,
        });
      }
    });
  };
  handleIsEdit = () => {
    this.props.setIsModal(true);
  };
  handleChange = (e) => {
    // const formData = this.state;
    let data = { ...this.state.formData };
    data[e.target.name] = e.target.value;
    this.setState({ formData: data });
  };
  handleBack = () => {
    this.props.history.goBack();
  };
  handleEditSubmit = (e) => {
    e.preventDefault();
    swal({
      title: "Berhasil!",
      text: "Kamu berhasil mengedit catatan ini.",
      icon: "success",
      button: false,
      timer: 2000,
    });
    // let data = this.state.state;
    if (this.state.formData.heading === "") {
      return false;
    }
    if (this.state.formData.content === "") {
      return false;
    }
    const id = this.props.match.params.id;
    const userData = JSON.parse(localStorage.getItem("userData"));
    const dataSubmit = {
      noteId: id,
      userId: userData.userId,
      heading: this.state.formData.heading,
      content: this.state.formData.content,
      date: "Modified " + formatDate(timestamp),
    };
    this.props.PutDataToAPI(dataSubmit);
    this.props.setIsModal(false);
  };
  render() {
    const notes = this.state.notes;
    return (
      <Fragment>
        <Container maxWidth="md">
          <div className="notes">
            <nav className="nav-notes">
              <div className="back-page">
                <BackNotes onClick={this.handleBack} />
              </div>
              <div className="action-page">
                <DeleteNotes onClick={this.handleDelete} />
                <EditNotes onClick={this.handleIsEdit} />
              </div>
            </nav>
            {/* {notes.map((note) => {
              return ( */}
            {/* <Fragment> */}
            <div className="heading">
              <h1>{notes.heading}</h1>
              <p className="date-note">
                <TodayIcon />
                {notes.date}
              </p>
            </div>
            <div className="body-content">
              <p className="content">{notes.content}</p>
            </div>
            {/* </Fragment> */}
            {/* );
            })} */}
          </div>
          {this.props.isEdit ? (
            <EditModal
              formData={this.state.formData}
              handleChange={this.handleChange}
              handleEditSubmit={this.handleEditSubmit}
            />
          ) : null}
        </Container>
      </Fragment>
    );
  }
}

const reduxState = (state) => ({
  isEdit: state.isEdit,
  isUpdate: state.isUpdate,
  // notes: state.notes,
});

const reduxDispatch = (dispatch) => ({
  setIsModal: (data) => dispatch(handleEdit(data)),
  getDataNotesFromAPI: (data) => dispatch(getDataNotesFromAPI(data)),
  PutDataToAPI: (data) => dispatch(PutDataToAPI(data)),
  DeleteDataToAPI: (data) => dispatch(DeleteDataToAPI(data)),
});

export default connect(reduxState, reduxDispatch)(Notes);
