import React, { useEffect, useState } from "react";
import TodayIcon from "@material-ui/icons/Today";
import "./CardNotes.scss";
import Masonry, { ResponsiveMasonry } from "react-responsive-masonry";
import { useDispatch, useSelector } from "react-redux";
import { getDataFromAPI } from "../../config/redux/action";

export const CardNotes = ({handleDetail, formData}) => {
  const dispatch = useDispatch();
  const userData = JSON.parse(localStorage.getItem("userData"));
  const notes = useSelector((state) => state.notes);
  const [notesData, setNotesData] = useState([]);
  const res = notes.filter(notes => notes.data.heading.toLocaleLowerCase().includes(formData.toLowerCase()));
  useEffect(() => {
    console.log("this: ", userData);
    dispatch(getDataFromAPI(userData.userId));
    setNotesData(res);
  }, [formData, res]);
  console.log(notes);
  return (
    <ResponsiveMasonry columnsCountBreakPoints={{ 800: 2, 900: 3, 1144: 4 }}>
      {notesData.length > 0 ? (
        <Masonry>
          {notesData.map((item) => {
            return (
              <div className="card-wrapper" key={item.id}>
                <button onClick={() => handleDetail(item.id)}>
                  <h3
                    className="judul"
                  >
                    {item.data.heading}
                  </h3>
                  <p className="date">
                    <TodayIcon />
                    {item.data.date}
                  </p>
                </button>
              </div>
            );
          })}
        </Masonry>
      ) : (
        <Masonry>
        <div className="card-wrapper-kosong">
          <button>
            <h3 className="judul-kosong">
              Catetan mu Masih Kosong.
            </h3>
            <p className="p-kosong">
              Ayo Buat sekarang! Klik Icon + di kanan Bawah.
            </p>
          </button>
        </div>
        </Masonry>
      )}
    </ResponsiveMasonry>
  );
};