import { Container } from "@material-ui/core";
import React from "react";
import { useHistory, useLocation } from "react-router-dom";
import "./ErrorPage.scss";
const errorImg = "./assets/img/notfound.svg";

const ErrorPage = () => {
  let location = useLocation();
  const history = useHistory();
  const handleBack = () => {
      history.goBack();
  }
  return (
    <div className="error-page">
      <Container maxWidth="sm">
        <div className="error-wrapper">
          <div className="error-img">
            <img src={errorImg} alt="error-image" />
          </div>
          <div className="error-body">
            <p className="heading">Oops!</p>
            <p className="sub-heading">
              The page of <span>{location.pathname}</span> doesn't exist.
            </p>
            <p className="text">Don't panic, just click button to go back <span>or Refresh page.</span></p>
            <button className="goback" onClick={handleBack}>Go Back</button>
            <button className="reload" onClick={()=> window.location.reload()}>Refresh</button>
          </div>
        </div>
      </Container>
    </div>
  );
};

export default ErrorPage;
